header{
	height: 100vh;
	padding-top:7rem;
	overflow: hidden;
}
.header__container{
	text-align: center;
	height: 100%;
	position: relative;
}


/*===================== CTA ====================*/
.cta{
	margin-top: 2.5rem;
	display: flex;
	gap: 1rem;
	justify-content: center;
}

/*===================== HEADER SOCIALS ====================*/

.header__socials{
	display: flex;
	flex-direction: column;
	text-align: center;
	gap: 0.8rem;
	position: absolute;
	left: 0;
	bottom: 5rem;
}
.header__socials::after{
	content:"";
	width: 1px;
	height: 2rem;
	background: var(--color-primary);

}

/*===================== ME IMAGE ====================*/
.me{
	width: 22rem ;
	height: 30rem;
	position: absolute;
	left:calc(50% - 11rem);
	margin-top: 3rem;
	border-radius: 12rem 12rem 0 0;
	overflow: hidden;
	
	
	
}

/*===================== SCROLL DOWN ====================*/
 .scroll__down{
 	font-weight: 200;
 	font-size: 12px;
 	position: absolute;
 	right: 2.3rem;
 	bottom: 5rem;
 	transform: rotate(90deg);
 }

 /*================== MEDIA QUERIES (MEDIUM DEVICES) =============*/

 @media screen and (max-width: 1024px){
 	header{
 		height: 100vh;


 	}
 }

  /*================== MEDIA QUERIES (SMALL DEVICES) =============*/

 @media screen and (max-width: 600px){
 	header{
 		height: 100vh;

 	}
 	.header__socials, .scroll__down{
 		display: none;
 	}

 }
