.testimonials__container{
	width: 40%;
	height: auto;
	
	

}
.client__avatar{
	width: 40%;
	padding: 5px;
	aspect-ratio:1/1;
	margin: 0 auto 1rem;

	
}
.client__avatar img{
	margin: 0 30%;
	width: 80px;
	height: 80px;
	border-radius: 50%;
}
.client__avatar h5{
	text-align: center;
}
.testimonial{
	background: var(--color-bg-variant);
	text-align: center;
	padding: 2rem;
	border-radius: 2rem;
	user-select: none;
	width: 100%;
	margin-bottom: 5px;
	height: auto;
}
.client__review{
	color: var(--color-light);
	font-weight: 300;
	width: 80%;
	margin: 0.8rem auto 0;
}

/*=============== MEDIA QUERIES (MEDIUM DEVICES) ===========================*/

@media screen and (max-width: 1024px){
		.testimonials__container{
			width: 60%;
		}
	}

/*=============== MEDIA QUERIES (SMALL DEVICES) ===========================*/

@media screen and (max-width: 600px){
		.testimonials__container{
			width: var(--content-width-sm);
		}
		.client__review{
			width: var(--content-width-sm);
		}
	
	}